import React, { useEffect, useState } from 'react';
import './App.css';
import Tiles from './components/Tiles';
import PostLoadingComponent from './components/PostDataLoading';

function App() {
	const PostLoading = PostLoadingComponent(Tiles);
	const [appState, setAppState] = useState({
		loading: false,
		tiles: null,
	});

	useEffect(() => {
		setAppState({ loading: true });
		const apiUrl = `http://127.0.0.1:8000/tile/`;
		fetch(apiUrl)
			.then((data) => data.json())
			.then((tiles) => {
				setAppState({ loading: false, tiles: tiles });
			});
	}, [setAppState]);
	return (
		<div className="App">
			<h1>Current Tiles</h1>
			<PostLoading isLoading={appState.loading} tiles={appState.tiles} />
		</div>
	);
}
export default App;